/*--------------------------------------------------------------
# Components
--------------------------------------------------------------*/
footer {
  border-top: none;
  background: #072b55;
  margin-top: 2rem;
  color: #ffffff;
  font-size: 14px;
  padding: 0.8rem 0;
}
footer .footer-menu .menu {
  overflow: hidden;
  padding: 0;
  margin: 0;
}
footer .footer-menu .menu .menu-item {
  list-style-type: none;
  float: left;
  margin-right: 10px;
}
footer .footer-menu .menu .menu-item a {
  margin: 4px 16px 4px 0;
  color: #ffffff;
  text-decoration: none;
}
footer .copyright {
  padding: 10px 0;
  text-align: center;
  display: none;
}

.header-container {
  background-color: #072b55;
  height: 6rem;
}
.header-container .container {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header-container .logo img {
  max-width: 140px;
  width: 140px;
  height: auto;
}

