h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.5rem;
}

h4 {
  font-size: 1.25rem;
}

h5 {
  font-size: 1.75rem;
}

h6 {
  font-size: 1rem;
}

body {
  font-family: "Noto Sans", sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: #1f1f1f;
  background-color: #ffffff;
  margin: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Poppins", sans-serif;
  font-weight: bold;
  line-height: 1.2;
}

.secondary-text, blockquote, figcaption {
  font-family: "Noto Sans", sans-serif;
}

